import { useForm } from 'react-hook-form';

import useAdminStore from '../hooks/useAdminStore';

export default function Register() {
  const adminStore = useAdminStore();

  const { register, handleSubmit } = useForm({ reValidateMode: 'onSubmit' });

  const make = async (data) => {
    const {
      name, identification, password,
    } = data;

    console.log(name);
    console.log(identification);
    console.log(password);

    await adminStore.register();
  };

  return (
    <form onSubmit={handleSubmit(make)}>
      <label htmlFor="input-name"></label>
      <input
        id="input-name"
        type="text"
        placeholder=" (2 ~ 10)"
        {...register('name')}
      />
      <label htmlFor="input-identification"></label>
      <input
        id="input-identification"
        type="text"
        placeholder=" 4 ~ 16    ."
        {...register('identification')}
      />
      <label
        htmlFor="input-password"
      >
        
      </label>
      <input
        id="input-password"
        type="password"
        placeholder="8  (), ,    ."
        {...register('password')}
      />
      <button
        id="signup"
        type="submit"
      >
        
      </button>
    </form>
  );
}
